.button-container {
    display: flex;
    justify-content: center; /* Center the buttons horizontally */
    margin-top: -40px; /* Add margin at the top for spacing */
}

.button-container button {
    margin: 40px; /* Add some margin between buttons for spacing */
    padding: 15px 30px; /* Adjust padding for better button appearance */
    background-color: #359d2f; /* Button background color */
    color: white; /* Button text color */
    border: none;
    border-radius: 5px; /* Add rounded corners */
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.button-container button:hover {
    background-color: #0056b3; /* Change background color on hover */
}

.input-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly; /* Evenly distribute child elements */
